import { StoreState } from '@/types';

const initialError = new Error('');
initialError.name = 'initial_error';
export default {
    user: {
        isLogin: false,
        userName: '',
        loading: false,
    },
    dashboard: {
        errors: {
            lastError: initialError,
            errorId: Math.random()
        },
        overview: {
            isLoading: false
        },
        realtime: {
            isLoading: false,
            recentEvents: [],
            recentJobs: []
        },
        server: {
            node: {
                isLoading: false,
                modalVisible: false,
                modalLoading: false,
                serverList: []
            },
            application: {
                isLoading: false,
                keyPairs: [],
                serverList: [],
                createModalVisible: false,
                createModalLoading: false
            }
        },
        spider: {
            list: {
                spiderList: [],
                isLoading: false,
                comfirmLoading: false,
                frequencyUpdateModalVisible: false,
                editSpiderModalVisible: false,
                editSpiderLoading: false,
                frequencyUpdateLoading: false,
                nowEditingSpider: {}
            }
        }
    },
} as StoreState;


